import Vue from 'vue'
import VueRouter from 'vue-router'
import Account from '../views/Account.vue'
import Shops from '../views/Shops.vue'
import Users from '../views/Users.vue'
import Shop from '../views/Shop.vue'
import User from '../views/User.vue'
import Login from '../views/Login.vue'
import Logout from '../views/Logout.vue'

Vue.use(VueRouter)

const routes = [

	{
		path: '/account',
		name: 'Account',
		component: Account,
		meta: {
			layout: 'main-layout',
			requiresAdmin: null
			}
	},
	{
		path: '/shops',
		name: 'Shops',
		component: Shops,
		meta: {
			layout: 'main-layout',
			requiresAdmin: true
			}
	},
	{
		path: '/users',
		name: 'Users',
		component: Users,
		meta: {
			layout: 'main-layout',
			requiresAdmin: true
			}
	},
	{
		path: '/shops/:id',
		name: 'Shop',
		component: Shop,
		meta: {
			layout: 'main-layout',
			requiresAdmin: true
			}
	},
	{
		path: '/users/:id',
		name: 'User',
		component: User,
		meta: { layout: 'main-layout' }
	},
	{
		path: '',
		name: 'Login',
		component: Login,
		meta: {
			layout: 'blank-layout',
			requiresAdmin: null
			}
	},
	{
		path: '/logout',
		name: 'Logout',
		component: Logout,
		meta: {
			layout: 'blank-layout',
			requiresAdmin: null
			}
	}
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

export default router
