import Vue from 'vue'
import { BootstrapVue } from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import Axios from 'axios'

import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.prototype.$http = Axios

const token = localStorage.getItem('token')
if (token) {
	Vue.prototype.$http.defaults.headers.common.Authorization = 'Token ' + token
}

router.beforeEach((to, from, next) => {
	if (!['Login', 'Signup'].includes(to.name) && !store.getters.loggedIn) {
		next({ name: 'Login' })
	}
	else if (to.matched.some(record => record.meta.requiresAdmin) && !store.getters.isAdmin) {
		next({ name: 'Account' })
	}
	else next()
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
