import axios from 'axios'

import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
axios.defaults.baseURL = process.env.VUE_APP_BASE_URL

export default new Vuex.Store({
	state: {
		token: localStorage.getItem('token') || '',
		userId: '',
		userType: ''
	},
	mutations: {
		getToken: (state, payload) => {
			state.token = payload.token
			state.userId = payload.userId
			state.userType = payload.userType
		},
		deleteToken: (state) => {
			state.token = ''
			state.userType = ''
		}
	},
	actions: {
		getToken ({ commit }, credentials) {
			return new Promise((resolve, reject) => {
				axios
					.post('http://localhost:8000/api/get_token', {
						username: credentials.username,
						password: credentials.password
					})
					.then(
						(response) => {
							console.log(response.data)
							const token = response.data.token
							const userId = response.data.id
							const userType = response.data.user_type
							localStorage.setItem('token', token)
							commit('getToken', { token, userType, userId })
							axios.defaults.headers.common.Authorization = 'Token ' + token
							resolve(response)
							})
					.catch(e => {
						console.log(e)
						reject(e)
						})
			})
		},
		deleteToken (context) {
			if (context.getters.loggedIn) {
				return new Promise((resolve, reject) => {
					axios.post('http://localhost:8000/api/delete_token')
					.then(response => {
							delete axios.defaults.headers.common.Authorization
							localStorage.removeItem('token')
							context.commit('deleteToken')
							resolve(response)
						})
					.catch(e => {
						console.log(e)
						reject(e)
						})
				})
			}
		},
		logout (context) {
			if (context.getters.loggedIn) {
				delete axios.defaults.headers.common.Authorization
				localStorage.removeItem('token')
				context.commit('deleteToken')
				}
			}
	},
	modules: {
	},
	getters: {
		loggedIn: state => state.token ? true : null,
		isAdmin: state => state.userType === 1 ? true : null
	}
})
