<template>
	<div class='container w-50'>
		<h1>
			{{user.username}}
			<b-button type="delete" variant="danger"  v-if = 'user.id' class = 'ml-2' @click = 'onDelete'>
				Delete
			</b-button>
		</h1>
		<transition name="fade">
			<b-form novalidate validated @submit="onSubmit" v-if="show">
				<b-form-group
					id="input-group-first_name"
					label="First name:"
					label-for="input-first_name"
				>
					<b-form-input
						id="input-first_name"
						v-model = 'user.first_name'
						@input = 'toogleChanged'
						required
					/>
				</b-form-group>
				<b-form-group
					id="input-group-last_name"
					label="Last name:"
					label-for="input-last_name"
				>
					<b-form-input
						id="input-last_name"
						v-model = 'user.last_name'
						@input = 'toogleChanged'
						required
					/>
				</b-form-group>
				<b-form-group
					id="input-group-shop"
					label="Shop:"
					label-for="input-shop"
				>
					<b-form-select
						id="input-shop"
						v-model="user.shop.id"
						:options = "shops"
						@change = 'toogleChanged'
						value-field = "id"
						text-field = "name"
						required
					/>
				</b-form-group>
				<div v-if = 'changed'>
					<b-button type="submit" variant="primary" v-if = 'user.id' class = 'mr-2'>Update</b-button>
					<b-button type="submit" variant="success"  v-else>Create</b-button>
				</div>
				<div v-else>
					<p>Enter or change User data!</p>
				</div>
			</b-form>
		</transition>
	</div>
</template>

<script>
import axios from 'axios'

export default {
	data: () => {
		return {
			user: { id: '', username: '', first_name: '', last_name: '', shop: { } },
			shops: [],
			show: false,
			changed: false,
			errors: []
		}
	},
	mounted () {
		if (parseInt(this.$route.params.id, 10)) {
			axios
				.get('api/user/' + this.$route.params.id)
				.then(response => {
					this.user = response.data
					if (!this.user.shop) {
						this.user.shop = { id: null }
						}
					this.show = true
					})
				.catch(e => {
				console.log(e)
				})
			}
		else { this.$router.push({ name: 'Users' }) }
		axios
			.get('api/list_shops')
			.then(response => {
				this.shops = response.data
				})
			.catch(e => {
			console.log(e)
			})
	},
	methods: {
		toogleChanged: function (event) {
			this.changed = true
			},
		onSubmit: function (event) {
			event.preventDefault()
			if (this.user.first_name && this.user.last_name && this.user.shop.id) {
				this.user.shop_id = this.user.shop.id
				if (this.user.id) {
					axios
						.put('api/user/' + this.$route.params.id + '/', this.user)
						.then(
							() => {
									this.$router.push({ name: 'Users' })
									this.changed = false
								}
							)
						.catch(e => {
							console.log(e)
						})
				}
			}
		},
		onDelete: function (event) {
			event.preventDefault()
			if (this.user.id === this.$store.state.userId) {
				axios
					.delete('api/user/' + this.user.id + '/')
					.then(() => this.$store.dispatch('logout'))
					.then(() => this.$router.push('/login'))
					.catch(e => {
						console.log(e)
					})
			}
			else {
				axios
					.delete('api/user/' + this.user.id + '/')
					.catch(e => {
						console.log(e)
					})
			}
		}
	}
}
</script>
