<template>
	<div  class = 'container'>
		<h1>
			Users
		</h1>
			<transition name="fade">
				<b-table responsive :fields="fields" :items="users" v-if ='!loading' caption-top>
					<template v-slot:table-caption>Click on a username to edit the User. Click on a shop name to edit the Shop.</template>
					<template v-slot:cell(username) = 'data'>
						<router-link :to='`/users/${ data.item.id}`'>
							{{ data.item.username }}
						</router-link>
					</template>
					<template v-slot:cell(shop) = 'data'>
						<router-link v-if= 'data.item.shop' :to='`/shops/${ data.item.shop.id}`'>
							{{ data.item.shop.name }}
						</router-link>
					</template>
				</b-table>
			</transition>
	</div>
</template>

<script>

export default {
	data: () => {
		return {
			users: [],
			fields: [
					{
						key: 'id',
						sortable: true
					},
					{
						key: 'username',
						sortable: true
					},
					{
						key: 'first_name',
						sortable: true
					},
					{
						key: 'last_name',
						sortable: true
					},
					{
						key: 'shop',
						sortable: true
					}
				],
			loading: true
		}
	},
	created () {
		this.$http
			.get('api/user/')
			.then(response => {
				this.users = response.data
				this.loading = false
				})
			.catch(e => {
			console.log(e)
			})
	}
}
</script>
