<template>
	<div class = 'container'>
		<h1>
			Shops
			<router-link :to='`/shops/create`'>
				<AddButton/>
			</router-link>
		</h1>
			<transition name="fade">
				<b-table responsive :fields="fields" :items="shops" v-if ='!loading' caption-top>
				<template v-slot:table-caption>Click on a shop name to edit the Shop. Click on an owner to edit the User. </template>
					<template v-slot:cell(name) = 'data'>
						<router-link :to='`/shops/${ data.item.id}`'>
							{{ data.item.name }}
						</router-link>
					</template>
					<template v-slot:cell(owner) = 'data'>
						<router-link :to='`/users/${ data.item.owner.id}`'>
							{{ data.item.owner.username }}
						</router-link>
					</template>
				</b-table>
			</transition>
	</div>
</template>

<script>
import AddButton from '@/components/elements/AddButton'

export default {
	components: { AddButton },
	data: () => {
		return {
			shops: [],
			fields: [
					{
						key: 'id',
						sortable: true
					},
					{
						key: 'name',
						sortable: true
					},
					{
						key: 'owner',
						sortable: true
					}
				],
			loading: true
		}
	},
	created () {
		this.$http
			.get('api/shop/')
			.then(response => {
				this.shops = response.data
				this.loading = false
				})
			.catch(e => {
			console.log(e)
			})
	}
}
</script>
