<template>
	<div class='container d-flex justify-content-center'>
		<b-card
			title="Login"
			style="max-width: 20rem;"
			border-variant = 'white'
		>
			<b-form novalidate validated @submit='onSubmit'>
				<b-form-group
					id='input-group-username'
					label='Username:'
					label-for='input-username'
				>
					<b-form-input
						id='input-username'
						v-model = 'username'
						@input = 'showSubmitButton'
						required
					/>
				</b-form-group>
				<b-form-group
					id='input-group-password'
					label='Password:'
					label-for='input-password'
					type='password'
				>
					<b-form-input
						id='input-password'
						v-model = 'password'
						@input = 'showSubmitButton'
						required
					/>
				</b-form-group>
				<transition name="fade">
					<b-button type='submit' variant='success' v-if = 'show' class = 'mr-2'>Login</b-button>
				</transition>
			</b-form>
		</b-card>
	</div>
</template>

<script>
export default {
	data: () => {
		return {
			username: null,
			password: null,
			show: null
		}
	},
	mounted () {
		this.$store.dispatch('deleteToken')
	},
	methods: {
		showSubmitButton: function (event) {
			if (this.username && this.password) {
					this.show = true
				}
			else {
					this.show = null
				}
			},
		onSubmit: function (event) {
			event.preventDefault()
			this.$store.dispatch('getToken', { username: this.username, password: this.password })
				.then(() => this.$router.push({ name: 'Account' }))
				.catch(e => alert('Please, check your login/password!'))
			}
	}
}
</script>
