<template>
	<div class='container w-50'>
		<h1>
			My account: {{user.username}}
			<b-button type="delete" variant="danger" class = 'ml-2' @click = 'onDelete'>
				Delete account
			</b-button>
		</h1>
		<transition name="fade">
			<b-form novalidate validated @submit="onSubmit" v-if="show">
				<b-form-group
					id="input-group-first_name"
					label="First name:"
					label-for="input-first_name"
				>
					<b-form-input
						id="input-first_name"
						v-model = 'user.first_name'
						@input = 'toogleChanged'
						required
					/>
				</b-form-group>
				<b-form-group
					id="input-group-last_name"
					label="Last name:"
					label-for="input-last_name"
				>
					<b-form-input
						id="input-last_name"
						v-model = 'user.last_name'
						@input = 'toogleChanged'
						required
					/>
				</b-form-group>
				<b-form-group
					id="input-group-shop"
					label="Shop:"
					label-for="input-shop"
				>
					<b-form-select
						id="input-shop"
						v-model="user.shop.id"
						:options = "shops"
						@change = 'toogleChanged'
						value-field = "id"
						text-field = "name"
						required
					/>
				</b-form-group>
				<div v-if = 'changed'>
					<b-button type="submit" variant="primary" class = 'mr-2'>Update</b-button>
				</div>
				<div v-else>
					<p>Enter or change your data!</p>
				</div>
			</b-form>
		</transition>
	</div>
</template>

<script>
import axios from 'axios'

export default {
	data: () => {
		return {
			user: { id: '', username: '', first_name: '', last_name: '', shop: {} },
			shops: [],
			show: false,
			changed: false,
			message: '',
			errors: []
		}
	},
	mounted () {
		axios
			.get('api/user/' + this.$store.state.userId)
			.then(response => {
				this.user = response.data
				if (!this.user.shop) {
					this.user.shop = { id: null }
					}
				this.show = true
				})
			.catch(e => {
			console.log(e)
			})
		axios
			.get('api/list_shops')
			.then(response => {
				this.shops = response.data
				})
			.catch(e => {
			console.log(e)
			})
	},
	methods: {
		toogleChanged: function (event) {
			this.changed = true
			},
		onSubmit: function (event) {
			event.preventDefault()
			if (this.user.first_name && this.user.last_name && this.user.shop.id) {
				this.user.shop_id = this.user.shop.id
				axios
					.put('api/user/' + this.$store.state.userId + '/', this.user)
					.then(
						() => {
								this.$router.push({ name: 'Account' })
								this.changed = false
							}
						)
					.catch(e => {
						console.log(e)
					})
			}
		},
		onDelete: function (event) {
			event.preventDefault()
			axios
				.delete('api/user/' + this.$store.state.userId + '/')
				.then(() => this.$store.dispatch('logout'))
				.then(() => this.$router.push('/login'))
				.catch(e => {
					console.log(e)
				})
		}
	}
}
</script>
