<template>
	<div class='container w-50'>
		<h1>
			{{shop.name}}
			<b-button type="delete" variant="outline-danger" v-if = 'shop.id' class = 'ml-2' @click = 'onDelete'>
				Delete
			</b-button>
		</h1>
		<transition name="fade">
			<b-form novalidate validated @submit="onSubmit" v-if="show">
				<b-form-group
					id="input-group-name"
					label="Name:"
					label-for="input-name"
				>
					<b-form-input
						id="input-name"
						v-model = 'shop.name'
						@input = 'toogleChanged'
						required
					/>
				</b-form-group>
				<b-form-group
					id="input-group-owner"
					label="Owner:"
					label-for="input-owner"
				>
					<b-form-select
						id="input-owner"
						v-model="shop.owner.id"
						:options = "users"
						@change = 'toogleChanged'
						value-field = "id"
						text-field = "username"
						required
					/>
				</b-form-group>
				<div v-if = 'changed'>
					<b-button type="submit" variant="primary" v-if = 'shop.id' class = 'mr-2'>Update</b-button>
					<b-button type="submit" variant="success"  v-else>Create</b-button>
				</div>
				<div v-else>
					<p>Enter or change Shop data!</p>
				</div>
			</b-form>
		</transition>
	</div>
</template>

<script>
import axios from 'axios'

export default {
	data: () => {
		return {
			shop: { id: '', name: '', owner: { } },
			users: [],
			show: false,
			changed: false,
			errors: []
		}
	},
	mounted () {
		if (parseInt(this.$route.params.id, 10)) {
			this.$http
				.get('api/shop/' + this.$route.params.id)
				.then(response => {
					this.shop = response.data
					this.show = true
					})
				.catch(e => {
				console.log(e)
				})
			}
		else { this.show = true }
		this.$http
			.get('api/list_users')
			.then(response => {
				this.users = response.data
				})
			.catch(e => {
			console.log(e)
			})
	},
	methods: {
		toogleChanged: function (event) {
			this.changed = true
			},
		onSubmit: function (event) {
			event.preventDefault()
			if (this.shop.name && this.shop.owner.id) {
				this.shop.owner_id = this.shop.owner.id
				if (this.shop.id) {
					this.$http
						.put('api/shop/' + this.$route.params.id + '/', this.shop)
						.then(
							() => {
									this.$router.push({ name: 'Shops' })
									this.changed = false
								}
							)
						.catch(e => {
							console.log(e)
						})
				}
				else {
					this.$http
						.post('api/shop/', this.shop)
						.then(
							() => {
									this.$router.push({ name: 'Shops' })
									this.changed = false
								}
							)
						.catch(e => {
							console.log(e)
						})
				}
			}
		},
		onDelete: function (event) {
			event.preventDefault()
			axios
				.delete('api/shop/' + this.shop.id + '/')
				.then(() => this.$router.push('/shops'))
				.catch(e => {
					console.log(e)
				})
		}
	}
}
</script>
